<template>
    <div>
        <div class="m-t-10">
            <p class="header-line"><i class="fa fa-pie-chart m-r-10"></i>{{$t('overview.overview')}}</p>
        </div>
        <div class="m-t-10">
            <div class="box-card">
                <div id="alldeviceschart"></div>
                <div id="onlinedevicestatuschart"></div>
                <div id="drawalldevicestypechart"></div>
            </div>
        </div>
        <div class="m-t-10">
            <p class="header-line"><i class="fa fa-object-group m-r-10"></i>{{$t('overview.groupStatus')}}</p>
        </div>
        <div class="m-t-10"> 
            <div class="box-card">
                <div class="overview" >
                    <div 
                    class="overview-box" 
                    v-for="item in groupData"
                    :key="item.gname"
                    :label="item.gname"
                    :value="item.gname">
                        <div class="overview-title">
                            <span class="left-title">
                                <i class="fa fa-windows m-r-5 m-l-10 c-primary fa-lg" aria-hidden="true" v-if="item.type== groupTypeArray.windows"></i>
                                <i class="fa fa-android m-r-5 m-l-10 c-primary fa-lg" aria-hidden="true" v-if="item.type== groupTypeArray.android"></i>
                                <i class="fa fa-linux m-r-5 m-l-10 c-primary fa-lg" aria-hidden="true" v-if="item.type== groupTypeArray.linux"></i>
                                <i class="fa fa-object-group m-r-5 m-l-10 c-primary fa-lg" aria-hidden="true" v-if="item.type== groupTypeArray.mix"></i>  
                                <span class="url pointer" @click="goDetails(item.gname)">
                                    {{item.gname}}
                                </span>
                            </span>
                            <span class="right-title fa fa-arrow-right m-r-10 pointer c-primary fa-lg" aria-hidden="true" @click="goDetails(item.gname)"></span>
                        </div>
                        <div class="overview-content">
                            <div style="border-right:2px solid #efefef">
                                <span>{{$t('overview.total')}}</span><br>
                                <span class="font-black">{{item.total}}</span><br>
                            </div>
                            <div style="border-right:2px solid #efefef">
                                <span>{{$t('overview.online')}}</span><br>
                                <span class="font-green">{{item.online}}</span><br>
                            </div>
                            <div style="border-right:2px solid #efefef">
                                <span>{{$t('overview.warning')}}</span><br>
                                <span v-if="item.warning==0" class="font-green">{{item.warning}}</span>
                                <span v-if="item.warning!=0" class="font-yellow">{{item.warning}}</span>
                            </div>
                            <div>
                                <span>{{$t('overview.error')}}</span><br>
                                <span v-if="item.error==0" class="font-green">{{item.error}}</span>
                                <span v-if="item.error!=0" class="font-red">{{item.error}}</span>
                            </div>
                        </div>
                    </div>
                </div>
                
            </div>
        </div>
    </div>    
</template>
<script>
import {getAllStatusAndGroupApi} from "../restfulapi/deviceGroupApi";
import {getAllDeviceStatus, getAllDevicesOs, getOnlineDeviceAbnormalStatus} from "../restfulapi/deviceStatusApi";
import handleResponse from "../../components/restfulapi/handleResponse";
import {mapState} from 'vuex';
import {groupType} from "../../assets/js/constant"
export default {
    name: 'overview',
    data() {
        return {
            groupData:[],
            alldeviceschart: null,
            groupTypeArray: groupType
        }
    },

    methods: {
        getOnlineDeviceAbnormalStatus(){
           getOnlineDeviceAbnormalStatus().then((data) => {
                handleResponse(data, (res) => {
                    if(Array.isArray(res)){
                        res.forEach(val => {
                            if(val.name ==='normal'){
                                val.name = this.$t('overview.normal');
                            }else if(val.name ==='error'){
                                val.name = this.$t('overview.error');
                            }else{
                                val.name = this.$t('overview.warning');
                            }
                            this.drawOnlineDeviceStatusChart(res);
                        });
                    }
                })
            })
        },

        getDeviceStatus(){
            getAllDeviceStatus().then((data) => {
                handleResponse(data, (res) => {
                    if(Array.isArray(res)){
                        res.forEach(val => {
                            if(val.name ==='online'){
                                val.name = this.$t('overview.online');
                            }else if(val.name ==='offline'){
                                val.name = this.$t('overview.offline');
                            }
                            this.drawAllDevicesChart(res);
                        });
                    }
                })
            })
        },
        getDeviceOs(){
            getAllDevicesOs().then((data) => {
                handleResponse(data, (res) => {
                    this.drawOnlineDevicesTypeChart(res);
                })
            })
        },
        getAllDevicesByGroups(){
            getAllStatusAndGroupApi().then((data) => {
                handleResponse(data, (res) => { 
                    if(Array.isArray(res)){
                        this.groupData = res;
                    }
                })
            })
        },
        goDetails(gname){
            this.$router.push({
                path: "/main/overview/groupstatus",
                query: {
                    gname: gname
                }
            })
        },
        drawAllDevicesChart(res){
            let onlineData = res;
            let option = {
                title: {
                    text: this.$t('overview.deviceStatus'),
                    left: 'center',
                    textStyle: {
                        fontWeight: 'bold',
                        fontSize: 16
                    }
                },
                tooltip: {
                    trigger: 'item',
                    formatter: '{a} <br/>{b} : {c} ({d}%)'
                },
                legend: {
                    orient: 'vertical',
                    left: 'left',
                    data: [onlineData[0].name, onlineData[1].name]
                },
                series: [
                    {
                        name: 'status',
                        type: 'pie',
                        radius: ['40%', '60%'],
                        center: ['50%', '60%'],
                        data: onlineData,
                        itemStyle: {
                            normal: {
                                label: {
                                    show: true,
                                    formatter: '{c} ({d}%)',
                                },
                                labelLine: {
                                    show: true,
                                }
                            }
                        },
                         
                    }
                ],
                color: ['green','red']
            }; 
            this.alldeviceschart = this.$echarts.init(document.getElementById("alldeviceschart"));
            this.alldeviceschart.setOption(option);
        },
        drawOnlineDeviceStatusChart(res){
            let statusData = res;
            let option = {
                title: {
                    text: this.$t('overview.abnormalStatus'),
                    left: 'center',
                    textStyle: {
                        fontWeight: 'bold',
                        fontSize: 16
                    }
                },
                tooltip: {
                    trigger: 'item',
                    formatter: '{a} <br/>{b} : {c} ({d}%)'
                },
                legend: {
                    orient: 'vertical',
                    left: 'left',
                    data: [statusData[0].name, statusData[1].name, statusData[2].name]
                },
                series: [
                    {
                        name: 'status',
                        type: 'pie',
                        radius: ['40%', '60%'],
                        center: ['50%', '60%'],
                        data: statusData,
                            itemStyle: {
                                normal: {
                                    label: {
                                        show: true,
                                        formatter: '{c} ({d}%)',
                                    },
                                    labelLine: {
                                        show: true,
                                    }
                                }
                        } 
                    }
                ],
                color: ['green', 'orange', 'red']
            };

            this.alldeviceschart = this.$echarts.init(document.getElementById("onlinedevicestatuschart"));
            this.alldeviceschart.setOption(option);
        },

        drawOnlineDevicesTypeChart(res){
            let statusData = res;
            let option = {
                title: {
                    text: 'OS',
                    left: 'center',
                    textStyle: {
                        fontWeight: 'bold',
                        fontSize: 16
                    }
                },
                tooltip: {
                    trigger: 'item',
                    formatter: '{a} <br/>{b} : {c} ({d}%)'
                },
                legend: {
                    orient: 'vertical',
                    left: 'left',
                    data: ["android", "windows", "linux"]
                },
                series: [
                    {
                        name: 'os',
                        type: 'pie',
                        radius: ['40%', '60%'],
                        center: ['50%', '60%'],
                        data: statusData,
                        itemStyle: {
                            normal: {
                                label: {
                                    show: true,
                                    formatter: '{c} ({d}%)',
                                },
                                labelLine: {
                                    show: true,
                                }
                            }
                        }  
                    }
                ],
                color: ['green', 'orange', 'red']
            };

            this.alldeviceschart = this.$echarts.init(document.getElementById("drawalldevicestypechart"));
            this.alldeviceschart.setOption(option);
        },

    },
    created() {
        this.getAllDevicesByGroups();
        this.getDeviceStatus();
        this.getOnlineDeviceAbnormalStatus();
        this.getDeviceOs();
    },  
    mounted(){
        
    },
    watch:{
        onlineDeviceCount(){
            this.getAllDevicesByGroups();
            this.getDeviceStatus();
            this.getOnlineDeviceAbnormalStatus();
            // this.getDeviceOs();
        },
       
        deviceStatusCount(){
            this.getAllDevicesByGroups();
            this.getOnlineDeviceAbnormalStatus();
            this.getDeviceOs();
        },
        switchLangCount(){
            this.getDeviceStatus();
            this.getOnlineDeviceAbnormalStatus();
        }
        

    },
    computed:{
        ...mapState({
            onlineDeviceCount: "onlineDeviceCount",
            deviceMsgCount: "deviceMsgCount",
            peripheralMsgCount: "peripheralMsgCount",
            switchLangCount: "switchLangCount",
            deviceStatusCount: "deviceStatusCount",
        }),   
    },
}
</script>
<style lang="scss" scoped>
@import "../../assets/css/colors";
    .font-black{
        font-weight: 600;
        font-size: 15px
    }
    .font-green{
        color:rgb(17,217,73);
        font-weight: 600;
        font-size: 15px
    }
    .font-yellow{
        color: orange;
        font-weight: 600;
        font-size: 15px
    }
    .font-red{
        color: red;
        font-weight: 600;
        font-size: 15px
    }
    .overview{
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        // justify-content: space-between;
        .overview-box{
            border: 2px solid #efefef;
            margin: 10px;
            height: 140px;
            flex: 0 0 auto;
            flex-basis: 340px;

            .overview-title{
                margin-top: 15px;
                padding-bottom: 15px;
                border-bottom: 2px solid #efefef;
                font-weight: 600;
                font-size: 16px;
                .left-title{
                    width: 80%;
                    display: inline-block
                }
                .right-title{
                    float:right;
                }
            }
            .overview-content{
                font-size: 15px;
                display: flex;
                flex-direction: row;
                div{
                    width: 25%;
                }
                span{
                    display: block;
                    margin-top: 5px;
                    height: 10px;
                }
                margin-top: 10px;
                text-align: center;
            }
        }
    }

    #alldeviceschart{
        height:300px;
        width:33%; 
        display: inline-block;
    }
    #onlinedevicestatuschart{
        height:300px;
        width:33%; 
        display: inline-block;
    }
    #drawalldevicestypechart{
        height:300px;
        width:33%; 
        display: inline-block;
    }
</style>



